p {
  font-size: 12px;
  margin: 0;
  padding: 0;
}

.card {
  background-color: lightgrey;
  border: 4px solid black;
  border-radius: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 260px;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  margin: 10px;
  opacity: 0.9;
  padding-bottom: 20px;
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  width: 200px;
}

.card p {
  margin-left: 18px;
}

.card:hover {
  background-color: #9b9b9b;
}

.school-name {
  font-size: 22px;
  padding: 0 8px;
  text-align: center;
}

.icon {
  margin-left: 30px;
  margin-right: 10px;
  height: 10px;
  position: relative;
  top: 1px;
}

.hide {
  display: none;
}



